    <h2>0. Input layer</h2>
    <p>
      The input layer represents the input image into the CNN. In image
      processing and classification, RGB images are used as input, the input
      layer has three channels, corresponding to the red, green, and blue
      channels, respectively, which are shown in this layer.
    </p>
    <p style="text-align: center;">
