        title="See Input Layer section">empirically observed</a
      > of a 7x7 RGB image.
    </div>

    <h2>1. Convolutional Layer</h2>
    <p>
      This is the first layer to extract various features from the input images.
      In this layer, the mathematical operation of convolution is performed
      between the input image and a kernal of a particular size, take MxM as an
      example. By sliding the filter over the input image, the dot product is
      taken between the filter and the parts of the input image with respect to
      the size of the filter (MxM). The output is termed as the feature map
      which gives us information about the image such as the corners and edges.
      Later, this feature map is fed to other layers to learn several other
      features of the input image.
    </p>
    <p style="text-align: center;">
      <img
        src="assets/figures/convlayer_detailedview_demo.gif"
        alt="clicking on topmost first conv. layer activation map"
        width="30%"
        height="30%"
        align="middle"
      />
    </p>
    <div class="figure-caption" style="text-align: center;">
      Figure 2. The kernel being applied to yield the topmost intermediate
      result for the discussed activation map.
    </div>

    <!-- 中间的间隔很奇怪 -->

    <h2>2. Pooling Layer</h2>
    <p>
      In most cases, a Convolutional Layer is followed by a Pooling Layer. The
      primary aim of this layer is to decrease the size of the convolved feature
      map to reduce the computational costs. In Max Pooling, the largest element
      is taken from feature map. Average Pooling calculates the average of the
      elements in a predefined sized Image section. The total sum of the
      elements in the predefined section is computed in Sum Pooling. The Pooling
      Layer usually serves as a bridge between the Convolutional Layer and the
      Fully Connected Layer.
    </p>
    <p style="text-align: center;">
      <img
        src="assets/figures/pooling.png"
        alt="This is the pooling demo pic"
        width="40%"
        height="40%"
        align="middle"
