    <ol>
      <li>
        <strong>Input size</strong> could be denoted as an n* n matrix. Particularly,
        it could be the value on each pixel in image processing.
      </li>
      <li>
        <strong>Padding</strong> is often necessary when the kernel extends beyond
        the activation map. Padding conserves data at the borders of activation maps,
        and it can help preserve the input's spatial size.
      </li>
      <li>
        <strong>Kernel size</strong> often also referred to as filter size, refers
        to the dimensions of the sliding window over the input. Choosing this hyperparameter
        has a massive impact on the image classification task.
      </li>
      <li>
        <strong>Stride</strong> indicates how many pixels the kernel should be shifted
        over at a time. As stride is decreased, more features are learned because
        more data is extracted.
      </li>
    </ol>

    <h2>3. Fully Connected Layer</h2>
    <p>
      The Fully Connected layer consists of the weights and biases along with
      the neurons and is used to connect the neurons between two different
      layers. These layers are usually placed before the output layer and form
      the last few layers of a CNN Architecture. In this, the input image from
      the previous layers are flattened and fed to the Fully Connected layer.
      The flattened vector then undergoes few more FC layers where the
      mathematical functions operations usually take place. In this stage, the
      classification process begins to take place.
    </p>
    <!-- svelte-ignore a11y-img-redundant-alt -->
    <p style="text-align: center;">
