    <p>
      The previous parts have shown the importance of CNN and how it can help
      people in demanding requests. However, sometimes the output can’t be
      simply expressed by a linear combination of the inputs. This is when
      Activation function comes in handy.
    </p>
    <h2><span>4. Activation</span> Functions</h2>

    <h6 id="article-relu">ReLU</h6>
    <p>
      While most of the functions are linear, there do exist some categories
      that can’t be expressed by a simple linear combination of the inputs.
      Rectified Linear Activation function (ReLU) is a good solution for these
      kind of problems. To be specific, ReLU provides non-linearity, and it is
      the most common of activation functions.
    </p>

    <div class="figure" style="text-align: center;">
      <!-- svelte-ignore a11y-img-redundant-alt -->
      <img
        src="assets/figures/relu.png"
        alt="ReLU image."
        width="30%"
        height="30%"
        align="middle"
      />
      <div class="figure-caption" style="text-align: center;">
        Figure 5. The ReLU activation function graphed.
      </div>
    </div>
    <p>
      ReLU has a derivative of either 0 or 1, depending on whether the input is
      negative or not. It means in fact that calculating the gradient of a
      neuron is computationally inexpensive. Thus CNNs using ReLU are much
      faster to train than other counterparts. The usage of ReLU helps to
      prevent the exponential growth in the computation required to operate the
      neural network. If the CNN scales in size, the computational cost of
      adding extra ReLUs grows linearly.
    </p>
    <p>
      ReLU is performed after every convolutional layer in the interactive
      network, by <strong>clicking ReLU</strong> in the below graph, you can observe
      the impact it has on the activation map.
    </p>

    <h6 id="article-softmax">Softmax</h6>
    <p>
      Softmax Layer is the last layer of CNN. It resides at the end of Fully
      Connected Layer. Softmax is a mathematical function that converts the
      model outputs into probabilities, where the probabilities of each value
      are proportional to the relative scale of each value in the vector.
      {softmaxEquation}
    </p>
    <p>
      Softmax outputs sum to 1 also makes great probability analysis. Softmax
      output is large if the score (input called logit) is large. Its output is
      small if the score is small. The proportion is not uniform. Softmax is
      exponential and enlarges differences - push one result closer to 1 while
      another closer to 0. It is more beneficial comparing to standard
      normalization because instead of solely giving a maximum value, Softmax
      assigns each output classification with a probability value, indicating
      the likelihood of the output belonging to that category.
    </p>

    <h2><span>5. Tiny VGG: A model example</span></h2>

    <p>Here, we will introduce a simple but classical CNN model.</p>
    <p>
      In 2016, the tiny VGG architecture was introduced in a research paper
      titled “CNN EXPLAINER: Learning Convolutional Neural Networks with
      Interactive Visualization ” by Wang et al. It is one of the most basic CNN
      architectures for machine learning beginners.
    </p>
    <p>
      It consists of 4 units and one softmax function. The first unit has a
      layer that consists of an input image with dimensions of 64×64. It is
      convolved with 10 filters of size 3×3 resulting in the dimension of
      62x62x10. Then it uses a relu activation function to generate the output
      of the first layer.
    </p>
    <p>
      The second unit consists of a 3×3 convolutional layer and the activation
      relu function, with the max pooling function of the stride 2. Hence the
      resulting image dimension will be 30×30×10.
    </p>
    <p>
      Similarly, the third unit involves a convolution the same as the first
      unit followed by the fourth unit with a similar filter size of 3×3 and
      stride of 2. Thus, the resulting image dimension will be reduced to
      13x13x10.
    </p>
    <p>
      Once the image dimension is reduced, the fifth unit is a fully connected
      layer to the final softmax output layer with n possible classes depending
      upon the number of classes in the dataset, in this scenario n = 10.
    </p>
    <p>
      The following graph is the visualization of the tiny VGG model. You can
