<style>
 
  @import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400&family=Nanum+Gothic&display=swap');
  
.container1 {  
  display: grid;
  grid-template-rows: 0.4fr 2.1fr 1.3fr;
  grid-auto-columns: 1fr;
  grid-auto-rows: 1fr;
  gap: 20px 19px;
  grid-auto-flow: row;
  width: 1150px;
  height: 1000px;
  margin:auto
}

.app {  display: grid;
  grid-template-columns: 1.2fr 0.7fr 1.2fr;
  grid-template-rows: 0.1fr 2.9fr 0.1fr;
  gap: 0px 0px;
  grid-auto-flow: row;
  grid-template-areas:
    ". . ."
    "drop botton process"
    ". . .";
  grid-area: 2 / 1 / 3 / 2;
}

.drop { grid-area: drop; }

.process {   display: grid; 
  grid-template-columns: 1fr; 
  grid-template-rows: 0.2fr 1.2fr; 
  gap: 0px 0px; 
  grid-template-areas: 
    "download"
    "preview"; 
  grid-area: process; 
}
.download { grid-area: download; }
.preview { grid-area: preview; }

.botton {  display: grid;
  grid-template-columns: 0.6fr 1.8fr 0.6fr;
  grid-template-rows: 1.1fr 0.5fr 1.4fr;
  gap: 0px 0px;
  grid-auto-flow: row;
  grid-template-areas:
    ". . ."
    ". submit ."
    ". . .";
  grid-area: botton;

}

.submit { grid-area: submit; }

.form-input {
  width:1.2fr;
  padding:10px;
  align-items:center;
  background:#fff;
  box-shadow: -3px -3px 7px rgba(94, 104, 121, 0.377),
              3px 3px 7px rgba(94, 104, 121, 0.377);
